.nav__list {
  position: relative;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.nav__link {
  text-decoration: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}
@media (max-width: 767px) {
  .nav__list {
    align-items: center;
    margin: 0;
    padding: 0;
  }
  .nav--position-mobile {
    position: absolute;
    top: 58px;
    z-index: 2;
    background-color: #ffffff;
    width: 320px;
  }
  .nav__toggle {
    border: none;
    background-color: inherit;
    cursor: pointer;
    position: absolute;
    width: 24px;
    height: 16px;
    top: -39px;
    right: 20px;
  }
  .nav--closed .nav__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }
  .nav--opened .nav__toggle::before,
  .nav--opened .nav__toggle::after {
    content: "";
    position: absolute;
    top: 8px;
    right: -1px;
    width: 25px;
    height: 2px;
    background-color: #000000;
  }
  .nav--opened .nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .nav--opened .nav__toggle::after {
    transform: rotate(-45deg);
  }
  .nav--nojs {
    position: static;
  }
  .nav--closed .nav__list {
    display: none;
  }
  .nav--nojs .nav__toggle {
    display: none;
  }
  .nav__link {
    padding: 13px 70px 19px;
  }
  .nav__link--home-mobile {
    padding: 12px 121px 20px;
  }
  .nav__item {
    padding: 20px 0 24px;
  }
  .nav__item::before {
    content: "";
    position: absolute;
    width: 320px;
    border: #e6e6e6 solid 1px;
    top: -1px;
    left: 0;
    box-shadow: 0 66px 0 0 #e6e6e6, 0 132px 0 0 #e6e6e6;
  }
}
@media (min-width: 768px) {
  .nav__list {
    flex-direction: row;
    width: 440px;
    justify-content: space-between;
  }
  .nav__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
  .nav__toggle {
    display: none;
  }
  .nav__link--current::after {
    content: "";
    width: 64px;
    height: 2px;
    background-color: #68b738;
    position: absolute;
    bottom: -8px;
    left: 1px;
  }
  .nav__link--form-current::after {
    content: "";
    width: 152px;
    height: 2px;
    background-color: #68b738;
    position: absolute;
    bottom: -8px;
    left: 1px;
  }
  .nav__link--catalog-current::after {
    content: "";
    width: 152px;
    height: 2px;
    background-color: #68b738;
    position: absolute;
    bottom: -8px;
    left: 1px;
  }
  .nav__item {
    position: relative;
  }
}
@media (min-width: 1200px) {
  .nav__list {
    flex-direction: row;
    width: 478px;
  }
  .nav__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
  }
  .nav__link--desktop-color {
    color: #111111;
  }
  .nav__link--current::after {
    background-color: #ffffff;
    width: 71px;
    left: 2px;
    bottom: -8px;
  }
  .nav__link--form-current::after {
    width: 168px;
    left: 2px;
    bottom: -8px;
  }
  .nav__link--catalog-current::after {
    content: "";
    width: 170px;
    bottom: -8px;
    left: 1px;
  }
}
